.nft-jacker.jacked {

    .shuffle-buttons.lit {
        .shuffle-btn {
            animation-name: push-rainbow;
            animation-duration: 1s;
            box-shadow: rgb(240 46 170 / 0%) -5px 5px, rgb(240 46 170 / 0%) -10px 10px, rgb(240 46 170 / 0%) -15px 15px, rgb(240 46 170 / 1%) -20px 20px, rgb(240 46 170 / 0%) -25px 25px;
        }
    }
    .shuffle-buttons {
        position: absolute;
        left: 0px;
        right: 0px;
        margin: auto;
        bottom: 0;
        top: 480px;
        height: 42px;
        width: 100px;
        display: flex;
   }

    .shuffle-btn:active {
        animation-name: push-rainbow;
        animation-duration: 4s;

        box-shadow: rgba(240, 46, 170, 0.4) -1px 1px, rgba(240, 46, 170, 0.3) -2px 2px, rgba(240, 46, 170, 0.2) -3px 3px, rgba(240, 46, 170, 0.1) -4px 4px, rgba(240, 46, 170, 0.05) -5px 5px;
    }

    .circle-icon {
        background-color: white;
        width: 20px;
        height: 20px;
        border-radius: 20px;
    }

    .right-triangle-icon {
         width: 0; 
         height: 0; 
         border-right: 10px solid transparent;
         border-left: 10px solid transparent;
         
         border-bottom: 20px solid white;
    }

    .shuffle-btn {
        margin-right: 10px;
        display: flex;
        border-radius: 10px;
        height: 42px;
        text-align: center;
        background-color: black;
        color: white;
        cursor: pointer;
        z-index: 1000;
        padding: 10px;


        img {
            width: 23px;
        }
    }
}

.nft-jacker {
    .shuffle-btn {
        display: none;
    }
}

@media screen and (max-width: 600px) {
    .nft-jacker.jacked {
        .buy-unsold-btn {
            display: none;
        }

        .shuffle-buttons {
            top: 560px;
        }

    }
}

@keyframes push-rainbow {
    from {
        background-color: #747474;
        box-shadow: rgba(240, 46, 170, 0.4) -5px 5px, rgba(240, 46, 170, 0.3) -10px 10px, rgba(240, 46, 170, 0.2) -15px 15px, rgba(240, 46, 170, 0.1) -20px 20px, rgba(240, 46, 170, 0.05) -25px 25px;
    }
    to {
        background-color: black;
        box-shadow: rgb(240 46 170 / 0%) -5px 5px, rgb(240 46 170 / 0%) -10px 10px, rgb(240 46 170 / 0%) -15px 15px, rgb(240 46 170 / 1%) -20px 20px, rgb(240 46 170 / 0%) -25px 25px;
    }
}
